
/*
  @iconRef - reference from  Webpack
*/
function addIcon (iconRef) {
 /*open node with class */
  let iNode = document.querySelectorAll("link[type='image/ico']");
  iNode[0].href = iconRef;
}
/** */
function addLinkToDOM (imgRef, className) {

  const xxx1 =  document.getElementsByClassName(className);
 
  if (xxx1.length > 0) {
    for (let a = 0; a < xxx1.length; a++) {
      xxx1[a].src = imgRef;
     
    }
  }

}

/**assign links from an array to a DOM  list*/ 
 /*@arrayRef - an array of links, they will be assign to the DOM lost items
*/
function addArrayLinksToDOM (arrayRef, className) {

  const xxx1 =  document.getElementsByClassName(className);
 
  if (xxx1.length > 0) {
    for (let a = 0; a < xxx1.length; a++) {
      xxx1[a].src = arrayRef[a];
     
    }
  }

}


export {addIcon,addLinkToDOM,addArrayLinksToDOM};